.App{
    background-color: var(--bg-color);
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
}

.container{
    width: 80%;
    display: flex;
    flex-direction: column;
}

@media screen and (min-width: 1200px) {
    .container{
        width: 60%;
    }
}
